import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Predicciones, RealTimeData, NewsResponse} from './Data'

@Injectable({
  providedIn: 'root'
})
export class DataService {
  constructor(private http: HttpClient){}

  predicciones = './assets/predicciones.json';
  dowjones = "https://api.iextrading.com/1.0/stock/DIA/chart/3m"
  sp500 = "https://api.iextrading.com/1.0/stock/SPY/chart/3m"
  russell2000 = "https://api.iextrading.com/1.0/stock/IWM/chart/3m"
  nasdaq = "https://api.iextrading.com/1.0/stock/NDAQ/chart/3m"

  news = "https://api.iextrading.com/1.0/stock/market/batch?symbols=dia,spy,iwm,ndaq&types=news&range=1m&last=5"

  getPredicciones(){
    return this.http.get<Predicciones>(this.predicciones);
  }

  getRealTimeDowJones(){
    return this.http.get<RealTimeData[]>(this.dowjones);
  }
  getRealTimeSP500(){
    return this.http.get<RealTimeData[]>(this.sp500);
  }
  getRealTimeRussell2000(){
    return this.http.get<RealTimeData[]>(this.russell2000);
  }
  getRealTimeNasdaq(){
    return this.http.get<RealTimeData[]>(this.nasdaq);
  }
  getNews(){
    return this.http.get<NewsResponse>(this.news);
  }
}
