import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { RealTimeData, NewsArticle, NewsResponse } from '../Data'
import * as Chart from 'chart.js'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  constructor(private dataService: DataService) {
  }

  lastData = [];
  news : NewsArticle[] = [];

  ngOnInit() {
    this.dataService.getRealTimeDowJones().subscribe((res: RealTimeData[]) => {
      this.lastData.push({ "stock": "Dow Jones", "data": res[res.length - 1] });
      this.buildChart(res, 'chart-realtimeDJ');
    });
    this.dataService.getRealTimeRussell2000().subscribe((res: RealTimeData[]) => {
      this.lastData.push({ "stock": "Russell 2000", "data": res[res.length - 1] });
      this.buildChart(res, 'chart-realtimeRUSSELL');
    });
    this.dataService.getRealTimeSP500().subscribe((res: RealTimeData[]) => {
      this.lastData.push({ "stock": "SP500", "data": res[res.length - 1] });
      this.buildChart(res, 'chart-realtimeSP');
    });
    this.dataService.getRealTimeNasdaq().subscribe((res: RealTimeData[]) => {
      this.lastData.push({ "stock": "NASDAQ", "data": res[res.length - 1] });
      this.buildChart(res, 'chart-realtimeNSDQ');
    });
    this.dataService.getNews().subscribe((res: NewsResponse) => {
      this.news = this.news.concat(res.DIA.news);
      this.news = this.news.concat(res.SPY.news);
      this.news = this.news.concat(res.IWM.news);
      this.news = this.news.concat(res.NDAQ.news);
    });
    }


  buildChart(data: RealTimeData[], chartName: string) {
    // Configuración de charts
    Chart.defaults.global.defaultFontFamily = "'Roboto', sans-serif";
    Chart.defaults.global.defaultFontColor = "#FFF";
    Chart.defaults.global.defaultFontStyle = 200;
    Chart.defaults.global.defaultFontSize = 14;

    var open: number[] = [];
    var high: number[] = [];
    var low: number[] = [];
    var close: number[] = [];
    var volume: number[] = [];
    var labels: string[] = [];
    var colors = [];
    var borderColors = [];
    for (var i = 0; i < data.length; i++) {
      open.push(data[i].open);
      high.push(data[i].high);
      low.push(data[i].low);
      close.push(data[i].close);
      volume.push(data[i].volume);
      labels.push(data[i].date);
      colors.push(data[i].close >= 0 ? 'rgba(0, 255, 0, 0.5)' : 'rgba(255, 0, 0, 0.5)');
      borderColors.push(data[i].close >= 0 ? 'green' : 'red');
    }

    new Chart(document.getElementById(chartName), {
      type: 'bar',
      data: {
        labels: labels,
        datasets: [{
          label: "Volumen",
          fill: true,
          backgroundColor: 'rgba(0, 255, 0, 0.1)',
          borderColor: 'rgba(0, 255, 0, 0.5)',
          borderWidth: 2,
          hoverBorderWidth: 3,
          data: volume,
          yAxisID: "y-axis-bar"
        },
        {
          label: "Open",
          data: open,
          borderColor: 'rgb(255,127,0)',
          fill: false,
          type: 'line',
          yAxisID: "y-axis-line"
        },
        {
          label: "High",
          data: high,
          borderColor: 'rgb(255, 0, 255)',
          fill: false,
          type: 'line',
          yAxisID: "y-axis-line"
        },
        {
          label: "Low",
          data: low,
          borderColor: 'rgb(255,0,127)',
          fill: false,
          type: 'line',
          yAxisID: "y-axis-line"
        },
        {
          label: "Close",
          data: close,
          fill: false,
          borderColor: 'rgba(255,0,0)',
          type: 'line',
          yAxisID: "y-axis-line"
        }],
      },
      options: {
        legend: {
          position: 'bottom',
          labels: {
          }
        },
        scales: {
          yAxes: [{
            position: "left",
            "id": "y-axis-line",
            ticks: {
              suggestedMin: 220,
              suggestedMax: 270,
              maxTicksLimit: 10,
            },
            scaleLabel: {
              display: true,
              labelString: "Puntos"
            }
          },
          {
            position: "right",
            "id": "y-axis-bar",
            ticks: {
              suggestedMin: 0,
              suggestedMax: 1600000,
              maxTicksLimit: 10,
            },
            scaleLabel: {
              display: true,
              labelString: "Volumen"
            }
          }]
        }
      }
    })
  }

  buildOHLC(data: Response, chartName:string) {
    var labels = ["DIA","SPY","IWM","NDAQ"];
    var close = [data["DIA"].close.price,
                data["SPY"].close.price,
                data["IWM"].close.price,
                data["NDAQ"].close.price];
    var open = [data["DIA"].open.price,
                data["SPY"].open.price,
                data["IWM"].open.price,
                data["NDAQ"].open.price];
    console.log(close);
    console.log(open);
    new Chart(document.getElementById("chart-OHLC"), {
      type: 'radar',
      data: {
        labels: labels,
        datasets: [
          {
            label: "Close",
            fill: true,
            backgroundColor: "rgba(255,0,0,0.1)",
            borderColor: "rgba(255,0,0)",
            pointBackgroundColor: "rgba(255,0,0)",
            pointBorderColor: "#fff",
            data: close,
            lineTension: -0.1
          },
          {
            label: "Open",
            fill: true,
            backgroundColor: "rgba(100,0,255,0.1)",
            borderColor: "rgba(100,0,255)",
            pointBackgroundColor: "rgba(100,0,255)",
            pointBorderColor: "#fff",
            data: open,
            lineTension: -0.1
          }
        ]
      },
      options: {
        legend: {
          position: 'bottom'
        },
        scale: {
          ticks: {
            suggestedMax:200,
            suggestedMin: 200,
            maxTicksLimit: 10,
            showLabelBackdrop: false,
            fontSize: 14
          },
          pointLabels: {
            fontSize: 15
          }
        }
      }
    });
  }



}
